class IngredientList extends Component {
    render() {
        const { data, filterText, addSelection, selection } = this.props;
        // const shuffledIngredients = shuffleArray(data);

    const ingredientList = data
      .filter(item => {
        return selection.indexOf(item.id) < 0
      })
      .filter(item => {
        return item.item.toLowerCase().indexOf(filterText.toLowerCase()) >= 0
      })
      .map(item => {
        return (
          <li 
            key={item.id} 
            className="ingredient"
            onClick={() => addSelection(item.id)}
            >
              {item.item}
          </li>
      )
    })

    return (
      <div>
        <ul>
        {ingredientList}
        </ul>
      </div>
    );
    } 
}

export default IngredientList;