import React, { Component } from 'react';
// import Rezepte from './rezepte/Rezepte'
// import ReactYoutubeExample from './ReactYoutubeExample'
// import ReactMarkdownExample from './ReactMarkDownExample';
import IngredientList from './components/IngredientList'
import Credit from './components/Credit'
import Search from './components/Search'
import ShortList from './components/ShortList';
import Suggestions from './components/Suggestions';

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      filterText: '',
      selection: []
    }
  }

  filterUpdate(value) {
    this.setState({
      filterText: value
    })
  }

  addSelection(id) {
    const sel_list = this.state.selection.concat([id])
    this.filterUpdate("")
    this.setState({
      selection: sel_list,
      // filterText: ''
    })
  }

  removeSelection(id) {
    const sel_list = this.state.selection
    var filteredAry = sel_list.filter(function(e) { return e !== id })
    this.setState({
      selection: filteredAry
    })
  }

  render() {
    const hasSearch = this.state.filterText.length > 0
    return(
      <div>
        <header>
        <Search 
        filterVal={this.state.filterText}
        filterUpdate={this.filterUpdate.bind(this)}
        />
        </header>
        <main>
        <ShortList 
        selection={this.state.selection}
        data={this.props.data}
        removeSelection={this.removeSelection.bind(this)}
        />
        { !hasSearch &&
            <Suggestions 
        data={this.props.data}
        ingredientID={this.props.ingredientID}
        selection={this.state.selection}
        addSelection={this.addSelection.bind(this)}
        />
          }
        <IngredientList 
        data={this.props.data}
        filterText={this.state.filterText}
        selection={this.state.selection}
        addSelection={this.addSelection.bind(this)}
        />
        {
          hasSearch &&
          <button
          onClick={this.filterUpdate.bind(this, '')}>
          Clear Search
          </button>
        }
        <Credit />
        </main>
      </div>
    )
  }
}

export default App;