import React from 'react'

export default ({data, selection, removeSelection}) => {
    var url_search = "https://www.google.de/search?q="
    const ingrList = selection.map(id => {
    url_search += data[id]['item']
    url_search += '+'
    const { item } = data[id]
    return (
        <li 
        key={id} className="selected"
        onClick={() => removeSelection(data[id]["id"])}
        >{item}</li>
    )
    })
    url_search += 'rezept'
    return (
        <div>
        {selection.length > 0 ? "" : <h4>Keine Zutat ausgewÃ¤hlt..</h4>}
        <ul>
            {ingrList}
        </ul>
        {selection.length > 0 ? <a 
                                href={url_search}
                                target="_blank"
                                rel="noopener noreferrer"><span 
                                                            role="img"
                                                            aria-label="backhand-index-pointing-right">ðŸ‘‰</span>Rezept suchen</a> : ""}
        </div>
    )
}