import React, { Component } from 'react'

class Suggestions extends Component {
    render() {
        function intersectB(firstSet, ...sets) {
            // function to intercept two sets
            var intersect = (a,b) => {
                return new Set([...a].filter(item => b.has(item)))
            };
        
            // iterate all sets comparing the first set to each.
            sets.forEach(sItem => firstSet = intersect(firstSet, sItem));
        
            // return the result.
            return firstSet;
        }

        var sets = []

        const { data, selection, addSelection, ingredientID } = this.props
        const SuggestionList = selection.map(sel => {
        let tempset = new Set(data[sel]['tgth_with'])
        sets.push(tempset)
        if (selection.length === sets.length) {
            var inter = intersectB(...sets);
            var intersection_list = [...inter]
        }
        if (intersection_list !== undefined) {
        return (
            intersection_list
        ).map(ingredient => {
            return (
                <li
                key={ingredientID[ingredient]} 
                className="suggestion"
                onClick={() => addSelection(ingredientID[ingredient])}
            >{ ingredient }</li>
            )
        })
        } else {
            return (
                console.log("ignore")
            )
        }
        })

        // console.log(results)

        return(
            <div className="suggestionbox">
                {SuggestionList.length === 0 ? "" : <h4>Beliebte Kombinationen:</h4>}
                <ul>
                    {SuggestionList}
                </ul>
            </div>
        )
    }
}

export default Suggestions;